import React from "react";
import moment from "moment";
import styled from "styled-components";

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin: 20px auto;
  //   @media (min-width: 768px) {
  border: 1px solid #ddd;
  //   }
`;

const Thead = styled.thead`
  @media (min-width: 768px) {
    border-bottom: 1px solid #ddd;
  }
  & > tr {
    @media only screen and (max-width: 767px) {
      display: none;
      //   position: absolute;
      //   top: -9999px;
      //   left: -9999px;
    }
  }
`;

const Tbody = styled.tbody`
  border: 1px solid #ccc;
`;

const Tr = styled.tr`
  border-bottom: 1px solid #ccc;
  @media (max-width: 767px) {
    display: grid;
    padding: 7px 0;
  }
`;

const Th = styled.th`
  padding: 10px;
`;

const Td = styled.td`
  ${props =>
    props.center && "text-align: center; padding: 10px 0px !important;"}
  @media (min-width: 768px) {
    padding: 5px;
  }
  @media (min-width: 992px) {
    padding: 10px;
  }
  @media only screen and (max-width: 767px) {
    padding: 3px 5px 3px 40%;
    border: none;
    position: relative;
    :before {
      position: absolute;
      top: 2px;
      left: 6px;
      width: 45%;
      padding-right: 10px;
      white-space: nowrap;
      content: attr(data-column);
      color: #333;
      font-weight: 500;
    }
  }
`;

const Span = styled.span`
  display: block;
  font-size: 13px;
  line-height: 1;
  color: #a1a1a1;
  :before {
    content: "\f017";
    display: inline-block;
    width: 15px;
    height: 10px;
    font-family: 'Font Awesome 5 Free';
`;

// const RemarkSpan = styled.span`
//   display: block;
//   font-size: 14px;
//   line-height: 1;
//   color: #a1a1a1;
// `;

export const DepositTable = ({
  depositData = [
    // {
    //   createdDate: "2020-08-11",
    //   createdTime: "11:22",
    //   remark: null,
    //   net: 200,
    //   bonus: 5,
    //   amount: 195
    // }
  ]
}) => {
  const renderRow = () => {
    return depositData.map(
      ({ createdDate, createdTime, remark, net, bonus, amount }) => (
        <Tr>
          <Td data-column="วันที่ฝาก">
            {moment(createdDate).format("D MMMM YYYY")}
            <Span>{createdTime}</Span>
          </Td>
          <Td data-column="ยอดฝาก">{amount}</Td>
          <Td data-column="โบนัส">{bonus}</Td>
          <Td data-column="ยอดที่ได้รับ">{net}</Td>
          <Td data-column="หมายเหตุ">{remark || (bonus && "ได้รับโบนัส")}</Td>
        </Tr>
      )
    );
  };
  return depositData.length === 0 ? (
    <Table id="history-deposit" cellspacing="0">
      <Tbody>
        <Tr>
          <Td center>ไม่พบข้อมูล</Td>
        </Tr>
      </Tbody>
    </Table>
  ) : (
    <Table id="history-deposit" cellspacing="0">
      <Thead>
        <Tr>
          <Th>วันที่ฝาก</Th>
          <Th>ยอดฝาก</Th>
          <Th>โบนัส</Th>
          <Th>ยอดที่ได้รับ</Th>
          <Th>หมายเหตุ</Th>
        </Tr>
      </Thead>
      <Tbody>{renderRow()}</Tbody>
    </Table>
  );
};

export const WithDrawTable = ({
  withdrawData = [
    // {
    //   createdDate: "2020-05-11",
    //   createdTime: "11:22",
    //   remark: null,
    //   net: 200,
    //   bonus: 5,
    //   amount: 195
    // }
  ]
}) => {
  const renderRow = () => {
    return withdrawData.map(({ createdDate, createdTime, remark, amount }) => (
      <Tr>
        <Td data-column="วันที่ฝาก">
          {moment(createdDate).format("D MMMM YYYY")}
          <Span>{createdTime}</Span>
        </Td>
        <Td data-column="ยอดถอน">{amount}</Td>
        <Td data-column="หมายเหตุ">{remark}</Td>
      </Tr>
    ));
  };
  return withdrawData.length === 0 ? (
    <Table id="history-deposit" cellspacing="0">
      <Tbody>
        <Tr>
          <Td center>ไม่พบข้อมูล</Td>
        </Tr>
      </Tbody>
    </Table>
  ) : (
    <Table id="history-deposit" cellspacing="0">
      <Thead>
        <Tr>
          <Th>วันที่ฝาก</Th>
          <Th>ยอดถอน</Th>
          <Th>หมายเหตุ</Th>
        </Tr>
      </Thead>
      <Tbody>{renderRow()}</Tbody>
    </Table>
  );
};

export default DepositTable;
