import React from "react";
import styled, { css } from "styled-components";
import moment from "moment";
import Wrapper from "../../components/page-wrapper";
import { GradientButton, WhiteButton } from "../../components/button";
import withApiWrapper from "../../utils/withApiWrapper";
import getUserToken, { getUserId } from "../../utils/session";
import { privatePath } from "../../utils/route-constants";
import { DepositTable, WithDrawTable } from "./table";

const widthCss = css`
  @media (max-width: 767px) {
    width: 100% !important;
  }
`;

const BtnEffect = css`
  background: rgb(1, 166, 253);
  background: linear-gradient(
    120deg,
    rgba(1, 166, 253, 1) 0%,
    rgba(23, 208, 207, 1) 80%
  );
  color: #fff;
`;

const BtnTab = styled.div`
  overflow: hidden;
  border: none;
`;

const ModeBtn = styled.button`
  background-color: #ddd;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  color: #333;
  font-weight: 600;
  width: 50%;
  @media (max-width: 425px) {
    font-size: 13px;
  }
  :hover {
    ${BtnEffect}
  }
  :focus {
    outline: none;
  }
  ${props => props.active && BtnEffect}
`;

const TableWrapper = styled.div`
  padding: 20px;
  border: 1px solid #ddd;
  border-top: none;
`;

const Form = styled.form`
  display: flex;
  justify-content: center;
  flex-direction: column;
  @media (min-width: 768px) {
    justify-content: space-around;
    flex-direction: row;
    max-width: 600px;
    margin: 0 auto;
  }
`;

const FormGroup = styled.div`
  margin-bottom: 10px !important;
  ${widthCss}
`;

const Label = styled.label`
  margin-right: 10px;
`;

const Input = styled.input`
  border-color: #17d0cf;
  color: #17d0cf;
  display: inline-block;
  width: 150px;
  @media (min-width: 576px) {
    vertical-align: middle;
  }
  ${widthCss}
`;

const SearchBtn = styled.button`
  font-size: 14px;
  font-weight: 600;
  padding: 4px 10px;
  background-color: #17d0cf;
  color: #fff;
  border-radius: 20px;
  :hover {
    text-decoration: none;
    color: #fff;
  }
  :focus {
    box-shadow: none;
  }
  ${widthCss}
`;

const InputWrapper = styled.div`
  display: grid;
  // width: 100%;
  ${widthCss}
`;

const Small = styled.small`
  color: red;
`;

const dateFormat = "YYYY-MM-DD";
const modes = { deposit: "DEPOSIT", withdraw: "WITHDRAW" };
const initDate = {
  startDate: moment().format(dateFormat),
  endDate: moment().format(dateFormat)
};

const Setting = ({ isLoading, isError, apiCall, closeErr }) => {
  const [mode, setMode] = React.useState(modes.deposit);
  const [date, setDate] = React.useState({ ...initDate });
  const [error, setError] = React.useState({});
  const [depositData, setDepositData] = React.useState([]);
  const [withdrawData, setWithdrawData] = React.useState([]);

  const handleChange = e => {
    const temp = { ...date };
    temp[e.target.name] = moment(e.target.value).format(dateFormat);
    setDate(temp);
  };

  const handleSubmit = async e => {
    e.preventDefault();
    const temp = {};
    const today = moment().format(dateFormat);
    if (date.startDate > today) {
      temp.startDate = "ต้องไม่เป็นวันในอนาคต";
    }
    if (date.endDate > today) {
      temp.endDate = "ต้องไม่เป็นวันในอนาคต";
    }
    if (date.startDate > date.endDate) {
      temp.startDate = "ต้องไม่เป็นวันหลังจากถึงวันที่";
    }
    setError(temp);
    if (JSON.stringify(temp) !== JSON.stringify({})) return;
    const action = mode;
    const data = {
      startDate: moment(date.startDate).format("YYYY/MM/DD"),
      endDate: moment(date.endDate).format("YYYY/MM/DD"),
      action
    };
    const res = await apiCall({
      method: "post",
      data,
      path: `/transaction/${getUserId()}`
    });
    if (res) {
      if (action === modes.deposit) {
        setDepositData(res);
      } else {
        setWithdrawData(res);
      }
    }
  };

  return (
    <Wrapper
      defaultTitle="ประวัติฝาก-ถอน"
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}>
      <div className="col-12">
        <BtnTab className="tab">
          <ModeBtn
            active={mode === modes.deposit}
            onClick={() => {
              setMode(modes.deposit);
            }}
            id="defaultOpen">
            ประวัติการฝาก
          </ModeBtn>
          <ModeBtn
            active={mode === modes.withdraw}
            onClick={() => {
              setMode(modes.withdraw);
            }}>
            ประวัติการถอน
          </ModeBtn>
        </BtnTab>
        <TableWrapper>
          <Form
            className="form-inline"
            id="deposit-form"
            onSubmit={handleSubmit}>
            <FormGroup className="form-group mb-2">
              <Label>จากวันที่</Label>
              <InputWrapper>
                <Input
                  type="date"
                  name="startDate"
                  value={date.startDate}
                  onChange={handleChange}
                  className="form-control"
                  id="start-date"
                />
                {error.startDate && <Small>{error.startDate}</Small>}
              </InputWrapper>
            </FormGroup>
            {/* <InputWrapper> */}
            <FormGroup className="form-group mb-2">
              <Label>ถึงวันที่</Label>
              <InputWrapper>
                <Input
                  type="date"
                  name="endDate"
                  value={date.endDate}
                  onChange={handleChange}
                  className="form-control"
                  id="end-date"
                />
                {error.endDate && <Small>{error.endDate}</Small>}
              </InputWrapper>
            </FormGroup>
            {/* </InputWrapper> */}

            <SearchBtn type="submit" className="btn mb-2">
              <span className="fas fa-search"></span>&nbsp;ค้นหา
            </SearchBtn>
          </Form>
          {mode === modes.deposit ? (
            <DepositTable depositData={depositData} />
          ) : (
            <WithDrawTable withdrawData={withdrawData} />
          )}
        </TableWrapper>
      </div>
    </Wrapper>
  );
};

export default withApiWrapper(Setting);
